






import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class SliderToggleBar extends Vue {
  @Prop({ default: false })
  isOn!: boolean;

  get btnClass() {
    return [
      'layout-button',
      'background-button',
      this.isOn ? 'on' : '',
    ];
  }
}
